import {getByKey ,setByKey} from '../../util/auth.js'
export default {
	data() {
		return {
			inspectionIndex: '1',
			content:'请扫描设备上的二维码获取风场相关信息',
			code:''
		}
	},
	onLoad() {

	},
	methods: {
		goScan(e){
			var _this = this
			uni.scanCode({
			    onlyFromCamera: true,
			    success: function (res) {
					_this.code = res.result
					setByKey('code',_this.code)
					uni.navigateTo({
						url:'inspectionHeader'
					})
			    }
			});
		}
	}
}
